<template>

    <HeaderArea />
    <div class="wrapper">

      <router-view v-slot="{ Component }">
        <transition :name="transitionName" appear>
            <component :is="Component" />
        </transition>
      </router-view>
    </div>
  
</template>

<script setup>

import { ref, watch } from 'vue'
import { useRoute } from 'vue-router'
import HeaderArea from '@/components/HeaderArea';

// 현재 라우트의 meta에서 transition 이름을 가져오는 computed 속성
const route = useRoute();
const transitionName = ref('fade');

// watch(() => route.meta.transition, (newTransition) => {
//   transitionName.value = newTransition || 'fade'
// }, { immediate: true })
const beforeDepth = ref(0)

watch(() => route.name, () => {
  console.log(route.meta.depth);
  
    if(route.meta.depth > beforeDepth.value) {
      transitionName.value = 'slide-right';  // slide-right
    } else if (route.meta.depth < beforeDepth.value ) {
      transitionName.value = 'slide-left';  // slide-left
    } else {
      transitionName.value = 'fade';
    }
    beforeDepth.value = route.meta.depth

});
</script>